import sassBotBar from './index.module.scss'
import {HomeOutlined,FileSearchOutlined,FileTextOutlined,ContactsOutlined} from '@ant-design/icons'
import {  Link,useLocation  } from "react-router-dom"
import React from 'react'
export default function BotBar(){
    const location = useLocation()
    let option = [
        {
            text:'首页',
            icon:<HomeOutlined style={{fontSize:'24px'}}/>,
            link:'/home'
        },
        {
            text:'找房',
            icon:<FileSearchOutlined style={{fontSize:'24px'}} />,
            link:'/renting'
        },
        {
            text:'咨询',
            icon:<FileTextOutlined style={{fontSize:'24px'}} />,
            link:'/home'
        },
        {
            text:'我的',
            icon:<ContactsOutlined style={{fontSize:'24px'}} />,
            link:'/home'
        },
    ]
    return (
        <div className={sassBotBar.botBar}>
            {option.map(item=>{
                return  <Link to={item.link} key={item.text}>
                            <div className={location.pathname==item.link?sassBotBar.Link:''} key={item.text}>{item.icon}<p>{item.text}</p></div>
                        </Link>
            })}
        </div>
    )
}